import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: { name: 'Home' }
    // name: 'home',
    // component: () => import(/* webpackChunkName: "home" */ '../views/HomeView.vue'),
  },
  {
    path: '/recibos/',
    name: 'Home',
    component: () => import(/* webpackChunkName: "home" */ '../views/HomeView.vue'),
  },
      {
        path: '/recibos/about',
        name: 'about',
        component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
      },    
      {
        path: '/recibos/recibos',
        name: 'listaDeRecibos',
        component: () => import(/* webpackChunkName: "about" */ '../views/RecibosView.vue')
      },          
      {
        path: '/recibos/empresas',
        name: 'listaDeEmpresas',
        component: () => import(/* webpackChunkName: "about" */ '../views/EmpresasView.vue')
      },       
      {
        path: '/recibos/empleados',
        name: 'listaDeEmpleados',
        component: () => import(/* webpackChunkName: "about" */ '../views/EmpleadosView.vue')
      },           
      {
        path: '/recibos/conceptos',
        name: 'listaDeConceptos',
        component: () => import(/* webpackChunkName: "about" */ '../views/ConceptosView.vue')
      },                     
]

const router = new VueRouter({
  routes,
  mode: process.env.IS_ELECTRON ? 'hash' : 'history',
})

export default router
